import axios from 'axios';
import authActions from './auth-actions';

axios.defaults.baseURL = 'https://goit-phonebook-api.herokuapp.com';

const token = {
  set(token) {
    axios.defaults.headers.common.Authorization = `Bearer ${token}`;
  },
  unset() {
    axios.defaults.headers.common.Authorization = '';
  }
};

//POST '/users/signup'
//credentials - body {name,email.password}
//–ü–æ—Å–ª–µ —É—Å–ø–µ—à–Ω–æ–π —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –¥–æ–±–∞–≤–ª—è–µ–º —Ç–æ–∫–µ–Ω –≤ HTTP-–∑–∞–≥–æ–ª–æ–≤–æ–∫

const register = credentials => async dispatch => {
  dispatch(authActions.registerRequest());

  try {
    const response = await axios.post('/users/signup', credentials);
    console.log('response.data', response.data);

    token.set(response.data.token);
    dispatch(authActions.registerSuccess(response.data));
  } catch (error) {
    dispatch(authActions.registerError(error.message));
  }
};


// POST @ /users/login
// body:
//   { email, password }
 
//  –ü–æ—Å–ª–µ —É—Å–ø–µ—à–Ω–æ–≥–æ –ª–æ–≥–∏–Ω–∞ –¥–æ–±–∞–≤–ª—è–µ–º —Ç–æ–∫–µ–Ω –≤ HTTP-–∑–∞–≥–æ–ª–æ–≤–æ–∫
 
const logIn = credentials => async dispatch => {
  dispatch(authActions.loginRequest());

  try {
    const response = await axios.post('/users/login', credentials);
   
    token.set(response.data.token);
    dispatch(authActions.loginSuccess(response.data));
  } catch (error) {
    dispatch(authActions.loginError(error.message));
  }
 };

// POST @ /users/logout - –æ—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–≥–æ–ª–æ–≤–æ–∫ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
// (—è–≤–Ω–æ –Ω–µ –ø–µ—Ä–µ–¥–∞—ë–º, –ø–æ—Ç–æ–º—É—á—Ç–æ –ø—Ä–∏ –ª–æ–≥–∏–Ω–µ –∏–ª–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –º—ã –µ–≥–æ –∑–∞—Å–µ—Ç–∏–ª–∏)
// headers:
//   Authorization: Bearer token
 
// 1. –ü–æ—Å–ª–µ —É—Å–ø–µ—à–Ω–æ–≥–æ –ª–æ–≥–∞—É—Ç–∞, —É–¥–∞–ª—è–µ–º —Ç–æ–∫–µ–Ω –∏–∑ HTTP-–∑–∞–≥–æ–ª–æ–≤–∫–∞
 
const logOut = () => async dispatch => {
   dispatch(authActions.logoutRequest());

  try {
    await axios.post('/users/logout');

    token.unset();
    //–¥–∞–Ω–Ω—ã–µ –Ω–∏–∫–∞–∫–∏–µ –Ω–µ –ø–µ—Ä–µ–¥–∞—ë–º, —ç—Ç–æ —á—Ç–æ–±—ã –æ—á–∏—Å—Ç–∏—Ç—å state
    // (—Å–±—Ä–æ—Å–∏—Ç—å –≤ –Ω–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ)
    dispatch(authActions.logoutSuccess());
  } catch (error) {
    dispatch(authActions.logoutError(error.message));
  }
 };

const getCurrentUser = credentials => dispatch => { };

export default {register, logIn, logOut, getCurrentUser }

